<template>
  <div class="covid">
    <form v-on:submit.prevent="getAll">
      <p>
        <select id="type" v-model="facet">
          <option value="NewConfirmed">Confirmados</option>
          <option value="NewDeaths">Mortos</option>
          <option value="NewRecovered">Recuperados</option>
        </select>
      </p>
      <p>
        <label for="quantity">nº paises:</label>
        <input type="number" id="quantity" min="1" max="10" v-model="top" />
      </p>
      <button type="submit">PESQUISA</button>
    </form>
    <section v-if="search">
      <div v-if="facet=='NewConfirmed'">
        <br />
        <img :src="imageNew" />
        <h2>{{facet}}</h2>
        <h5>{{date}}</h5>
        <div v-for="ele in summary" :key="ele">
          <table>
            <tr>
              <th>{{ele.Country}}</th>
            </tr>
            <tr>
              <td>{{ele.NewConfirmed}}</td>
            </tr>
          </table>
        </div>
      </div>
      <div v-else-if="facet=='NewDeaths'">
        <img :src="imageDeath" />
        <h2>{{facet}}</h2>
        <h5>{{date}}</h5>
        <div v-for="ele in summary" :key="ele">
          <table>
            <tr>
              <th>{{ele.Country}}</th>
            </tr>
            <tr>
              <td>{{ele.NewDeaths}}</td>
            </tr>
          </table>
        </div>
      </div>
      <div v-else-if="facet=='NewRecovered'">
        <img :src=" imageRecovery" />
        <h2>{{facet}}</h2>
        <h5>{{date}}</h5>
        <div v-for="ele in summary" :key="ele">
          <table>
            <tr>
              <th>{{ele.Country}}</th>
            </tr>
            <tr>
              <td>{{ele.NewRecovered}}</td>
            </tr>
          </table>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import imageNew from "../assets/new.png";
import imageDeath from "../assets/death.jpg";
import imageRecovery from "../assets/recovery.jpg";

export default {
  name: "covidCountry",
  data() {
    return {
      imageNew: imageNew,
      imageDeath: imageDeath,
      imageRecovery: imageRecovery,
      summary: "",
      facet: "",
      top: 0,
      date: "",
      summaryConfirmed: "",
      search: false
    };
  },
  methods: {
    getAll() {
      this.getCountry();
      this.getDate();
      this.sort();
    },
    getCountry() {
      fetch("https://api.covid19api.com/summary")
        .then(response => response.json())
        .then(data => (this.summary = data.Countries));
      this.search = true;
    },
    getDate() {
      this.date = new Date().toLocaleString();
    }
  }
};
</script>

